import React, {useState, useEffect, useRef} from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { createStyles, fade, Theme, makeStyles } from '@material-ui/core/styles';
import MenuIcon from '@material-ui/icons/Menu';
import Sidebar from './Sidebar';
import '../../styles/logo.css';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
      width: '100%',
      minWidth: '600px',
      display: 'block',
      marginLeft: 'auto',
      marginRight: 'auto',
    },
    appbar: {
      position: 'fixed',
      zIndex: 0,
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      flexGrow: 1,
      display: 'none',
      [theme.breakpoints.up('sm')]: {
        display: 'block',
      },
    }
  }),
);

function Header() {
  const classes = useStyles();
  const [sidebarState, setSidebarState] = useState(true);

  const toggleDrawer = (open: boolean) => {
    setSidebarState(open);
  };
  const [size, setSize] = useState(15);
  useEffect(() => {
    const handleScroll = () => {
      if (document.body.scrollTop > 50 || document.documentElement.scrollTop > 50) {
        setSize(2.8);
      } else {
        setSize(15);
      }
    };

    window.addEventListener("scroll", handleScroll, { passive: true });

    return () => window.removeEventListener("scroll", handleScroll);
  });



  return (
    <div className={classes.root}>
      <AppBar className={classes.appbar}>
        <Toolbar>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="open drawer"
            onClick={() => { 
              toggleDrawer(!sidebarState); 
            }}
          >
            <MenuIcon />
          </IconButton>
          <Typography className={classes.title} variant="h6" noWrap>
            Alin-Daniel Ferenczi
          </Typography>
        </Toolbar>
      </AppBar>
      <Sidebar toggleDrawer={toggleDrawer} left={sidebarState}/>
      <div style={{height:size}} id="logo-container">
        <img src={process.env.PUBLIC_URL+"/logo.png"} style={{height:size+"em", top:size-2.1+"em"}} className="App-logo" alt="logo" />
      </div>
    </div>
  );
}


export default Header;