import React, { Component, useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import '../../styles/home.css'
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';

function Home() {
    const [size, setSize] = useState(15);

    useEffect(() => {
        const handleScroll = () => {
            if (document.body.scrollTop > 50 || document.documentElement.scrollTop > 50) {
                setSize(2.8);
            } else {
                setSize(15);
            }
        };

        window.addEventListener("scroll", handleScroll, { passive: true });

        return () => window.removeEventListener("scroll", handleScroll);
    });

    return (
        <div style={{ marginTop: "30em" }} id="logo-container">
            <ArrowDropDownIcon />
            <div>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                Content<br/>
                </div>
        </div>
    );
}

export default Home;